
import "./style.css";
import React, { useState } from 'react';
import ReactModal from "react-modal";

ReactModal.setAppElement('#root');
export default function TransactionCard(transactionData) {

  const [transactionDetailsModalState, setTransactionDetailsModalState] = useState(false);

  return (
    <div className="TransactionCard"
      onClick={() => setTransactionDetailsModalState(true)}
    >
      <p>Transaction #{transactionData.TransactionID}</p>
      <span style={{
        display: 'grid',
        gridTemplateColumns: 'auto',
        alignItems: 'center'
      }}>Sender <b>{transactionData.SenderUniqueID}</b></span>
      <span style={{
        display: 'grid',
        gridTemplateColumns: 'auto',
        alignItems: 'center'
      }}>Receiver <b>{transactionData.ReceiverUniqueID}</b></span>

    <ReactModal 
      isOpen={transactionDetailsModalState}
      onRequestClose={() => setTransactionDetailsModalState(false)}
      style={{
        overlay: {
          backgroundColor: 'rgba(0, 0, 0, 0 )',
          marginRight: 'auto',
          marginLeft: 'auto',
          marginTop: 'auto',
          marginBottom: 'auto',
          width: '900px',
          height: '700px',
          overflow: 'hidden',
          boxSizing: 'border-box'
        },
        content: {
          border: 'none',
          boxShadow: '12px 24px 32px rgba(0, 0, 0, 0.225)'
        }
      }}
    >
        <div className="TransactionDetailsModal">
          <span>
            <p>Unique Hash Address</p>
            <h3 className="unique-id">{transactionData.TransactionUniqueHashAddress.words}</h3>
          </span>

          <span>
            <p>Transaction done by</p>
            <span><b>{transactionData.FirstName} {transactionData.LastName}</b></span>
          </span>

          <span>
            <p>Sender ID</p>
            <span><b>{transactionData.SenderUniqueID}</b></span>
          </span>

          <span>
            <p>Receiver ID</p>
            <span><b>{transactionData.ReceiverUniqueID}</b></span>
          </span>

          <span>
            <p>Amount of <u>{transactionData.TransactionCurrency}</u> transferred</p>
            <span><b>{transactionData.TransactionAmount}</b></span>
          </span>

        </div>
    </ReactModal>
    </div>
    
  )
}