import React, { useState } from 'react';
import TransactionCard from '../TransactionCard/TransactionCard';
import "./style.css";


export default function BlockchainContainer() {
  
  const [transactionState] = useState(JSON.parse(localStorage.getItem('transactions')));
  console.log('transaction data> ', transactionState)

  if (JSON.parse(localStorage.getItem('transactions')) === '\0') {
    console.log('1272632')
    localStorage.setItem('transactions', JSON.stringify([{
      'firstname': '',
      'lastname': '',
      'amount': '',
      'currency_name': '',
      'sender_id': '',
      'receiver_id': '',
      'unique_hash': ''
    }]))
  } else if (JSON.parse(localStorage.getItem('transactions')) !== '\0') {
    console.log('000000000')
  } else {
    console.log('working')
  }

  return (
    <div className="BlockchainContainer">
      {
        transactionState.map((data, index) => (
          <TransactionCard TransactionID={index}
            FirstName={data.firstname}
            LastName={data.lastname}
            TransactionAmount={data.amount}
            TransactionCurrency={data.currency_name}
            SenderUniqueID={data.sender_id}
            ReceiverUniqueID={data.receiver_id}
            TransactionUniqueHashAddress={data.unique_hash}
          />
      ))}
    </div>
  )
}

